import React, {Component} from 'react';
import ItemList from '../list/item-list';

class Form extends Component {
    constructor(props) {
      super(props);
      this.state = {
        username: '',
        password: '',
        data: [],
        content: false
      };
  
      // this.usernameInput = this.usernameInput.bind(this);
      // this.passwordInput = this.passwordInput.bind(this);
      // this.handleSubmit = this.handleSubmit.bind(this);
    }
  
    usernameInput(event) {
      this.setState({username: event.target.value});
      this.state.content = false;
    }

    passwordInput = (event) => {
      this.setState({password: event.target.value});
      this.state.content = false;
    }
  
    handleSubmit(event) {
      this.state.content = true;
      const item = {name: this.state.username, password: this.state.password};
      this.state.data.push(item);

      event.preventDefault();
    }
  
    render() {
      const listItem = this.state.content ? <ItemList value={this.state}></ItemList> : '';
      return (
        <div>
         
        
        <form onSubmit={(e) => this.handleSubmit(e)}>
          <label>
            Usenamer:
            </label>
            <input type="text" value={this.state.username} onChange={(e) => this.usernameInput(e)} />
            <label>
            Password:
            </label>
            <input type="password" value={this.state.password} onChange={this.passwordInput} />
          
          <input type="submit" value="Submit" />
        </form>
          {listItem}
        
        </div>
      );
    }
  }

  export default Form;


